import React from 'react';
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";

import Header from './Header';
import TodoList from './TodoList';
import Footer from './Footer';
import '../css/app.css';

function uuid() {
  let i, random;
  let uuid = '';

  for (i = 0; i < 32; i++) {
    random = Math.random() * 16 | 0;
    if (i === 8 || i === 12 || i === 16 || i === 20) {
      uuid += '-';
    }
    uuid += (i === 12 ? 4 : (i === 16 ? (random & 3 | 8) : random))
      .toString(16);
  }

  return uuid;
}

class TodoApp extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      newTodo: '',
      todos: [],
    };
  }

  handleChange(event) {
    this.setState({ newTodo: event.target.value });
  }

  handleNewTodoKeyDown(event) {
    if (event.keyCode !== 13) { //ENTER_KEY
      return;
    }
    event.preventDefault();

    let val = this.state.newTodo.trim();
    if (val) {
      let todos = this.state.todos.slice();
      todos.push({
        id: uuid(),
        title: val,
        completed: false
      });
      this.setState({
        todos: todos,
        newTodo: '',
      });
    }
  }

  toggle(todoToggle) {
    let todos = this.state.todos.slice();
    todos = todos.map((todo) => {
      return todo !== todoToggle ? todo : Object.assign({}, todo, { completed: !todo.completed });
    });
    this.setState({
      todos: todos
    });
  }

  destroy(todoDestroy) {
    let todos = this.state.todos.slice();
    todos = todos.filter((todo) => {
      return todo !== todoDestroy;
    });
    this.setState({
      todos: todos
    })
  }

  save(todoSave, val) {
    let todos = this.state.todos.slice();
    todos = todos.map((todo) => {
      return todo !== todoSave ? todo : Object.assign({}, todo, { title: val });
    });
    this.setState({
      todos: todos
    })
  }

  clearCompleted() {
    let todos = this.state.todos.slice();
    todos = todos.filter((todo) => {
      return !todo.completed;
    });
    this.setState({
      todos: todos
    })
  }

  render() {
    const todos = this.state.todos;
    const activeCount = todos.reduce((acNum, todo) => {
      return todo.completed ? acNum : acNum + 1;
    }, 0);
    const completedCount = todos.length - activeCount;

    let footer = null;
    if (activeCount || completedCount) {
      footer = (
        <Footer
          count={activeCount}
          completedCount={completedCount}
          onClearCompleted={this.clearCompleted.bind(this)}
        />
      );
    }
    return (
      <section className='todoapp'>
        <Header
          value={this.state.newTodo}
          handleKeyDown={this.handleNewTodoKeyDown.bind(this)}
          handleChange={this.handleChange.bind(this)}
        />
        <section className='main'>
          <Router basename='/todo-demo/'>
            <Switch>
            <Route
              path='/active'
              render={props => (
                <TodoList
                  {...props}
                  todos={this.state.todos}
                  toggle={this.toggle.bind(this)}
                  save={this.save.bind(this)}
                  destroy={this.destroy.bind(this)}
                  nowShowing='active'
                />
              )}
            />
            <Route
              path='/completed'
              render={props => (
                <TodoList
                  {...props}
                  todos={this.state.todos}
                  toggle={this.toggle.bind(this)}
                  save={this.save.bind(this)}
                  destroy={this.destroy.bind(this)}
                  nowShowing='completed'
                />
              )}
            />
            <Route
              path='/'
              render={props => (
                <TodoList
                  {...props}
                  todos={this.state.todos}
                  toggle={this.toggle.bind(this)}
                  save={this.save.bind(this)}
                  destroy={this.destroy.bind(this)}
                  nowShowing='all'
                />
              )}
            />
            </Switch>
            {footer}
          </Router>
        </section>
      </section>
    )
  }
}

export default TodoApp;
